:root {
  --purple: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --dark-purple: hsl(270, 9%, 17%);
  --gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --grayish-purple: hsl(273, 4%, 51%);
}

.section-four {
  background-color: var(--gray);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 500;
  line-height: 1rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-bottom: 2rem;
  padding-top: 3rem;
}
.section-four ul {
  list-style-type: none;
}

.top {
  display: flex;
  justify-content: space-between;
  min-width: 48rem;
  padding-bottom: 1.7rem;
  border-bottom: 0.02rem solid var(--grayish-purple);
}

.sm {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.sm a {
  color: var(--grayish-purple);
}
.sm a:hover {
  color: var(--purple);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.smlogosvg {
  fill: currentColor;
}

.links {
  padding-top: 1.7rem;
  min-width: 48rem;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  line-height: 1.4rem;
}
.links a {
  text-decoration: none;
  color: var(--purple);
}
.links a:hover {
  text-decoration: underline;
  transition: 0.3s;
}

.title {
  padding-bottom: 1rem;
  color: var(--grayish-purple);
}